import React from 'react';
import { mockData } from '../../data/mockData';
import { CardContent, Typography } from '@mui/material';
import { CenteredHeading, CardGrid, CardCustom } from './menuItem.styles';

const MenuItem = ({ menuType }) => {
	const items = mockData[menuType] || [];

	const groupByCategory = (items) => {
		return items.reduce((acc, item) => {
			if (!acc[item.category]) {
				acc[item.category] = [];
			}
			acc[item.category].push(item);
			return acc;
		}, {});
	};

	const groupedItems = menuType === 'drinks' ? groupByCategory(items) : { [menuType]: items };

	const categoryTranslations = {
		redWine: 'Vin Rouge 75cl',
		whiteWine: 'Vin Blanc 75cl',
		roseWine: 'Vin Rosé 75cl',
		champagne: 'Champagne',
		beer: 'Bière',
		tea: 'Thé',
		coffee: 'Café',
		juice: 'Jus',
		softDrink: 'Boisson Gazeuse',
	};

	return (
		<div>
			{Object.entries(groupedItems).map(([category, items]) => (
				<div key={category}>
					{menuType === 'drinks' && <CenteredHeading>{categoryTranslations[category]}</CenteredHeading>}
					<CardGrid isDrinks={menuType === 'drinks'}>
						{items.map((item) => (
							<CardCustom isDrinks={menuType === 'drinks'} key={item.id}>
								<CardContent>
									<Typography variant='subTitle' component='p'>
										{item.name}
									</Typography>
									<Typography variant='cardBody' component='p' mt={1}>
										{item.description}
									</Typography>
									<Typography variant='subBody' component='p' mt={1}>
										Prix: {item.price.toFixed(2)} €
									</Typography>
								</CardContent>
							</CardCustom>
						))}
					</CardGrid>
				</div>
			))}
		</div>
	);
};

export default MenuItem;
