import * as React from 'react';
import { Box } from '@mui/material';
import { TabContext } from '@mui/lab';
import MenuItem from '../menu/menuItem';
import { StyledTab, StyledTabList, StyledTabPanel } from './tabLabel.styles';

const TabLabel = () => {
	const [value, setValue] = React.useState('starters');

	const handleChange = (event, newValue) => {
		setValue(newValue);
	};

	return (
		<Box sx={{ display: 'flex', justifyContent: 'center' }}>
			<Box>
				<TabContext value={value}>
					<StyledTabList onChange={handleChange} centered>
						<StyledTab label='Entrées' value='starters' />
						<StyledTab label='Plats' value='mains' />
						<StyledTab label='Desserts' value='desserts' />
						<StyledTab label='Boissons' value='drinks' />
					</StyledTabList>

					<StyledTabPanel value='starters'>
						<MenuItem menuType='starters' />
					</StyledTabPanel>
					<StyledTabPanel value='mains'>
						<MenuItem menuType='mains' />
					</StyledTabPanel>
					<StyledTabPanel value='desserts'>
						<MenuItem menuType='desserts' />
					</StyledTabPanel>
					<StyledTabPanel value='drinks'>
						<MenuItem menuType='drinks' />
					</StyledTabPanel>
				</TabContext>
			</Box>
		</Box>
	);
};

export default TabLabel;
