import React from 'react';
import {
	Container,
	Wrapper,
	TextContainer,
	StyledTypography,
	GridItem,
	StyledCard,
	StyledCardMedia,
	CardContentStyled,
	TypographyStyled,
} from './about.styles';
import { Grid, Typography } from '@mui/material';
import { Link } from 'react-router-dom';

const cardInfo = [
	{ title: 'Saison', image: 'https://snrtn.github.io/assets/logo/seoullab_logo_art.png' },
	{ title: 'Entrées', image: 'https://snrtn.github.io/assets/index/food.png' },
	{ title: 'Boissons', image: 'https://snrtn.github.io/assets/index/drank.png' },
	{ title: 'Plats', image: 'https://snrtn.github.io/assets/logo/seoullab_logo_art.png' },
];

const About = () => {
	return (
		<Container>
			<Wrapper>
				<TextContainer>
					<StyledTypography variant='title' size='3rem' height='3rem'>
						Cuisine coréenne moderne
					</StyledTypography>
					<StyledTypography variant='subTitle' size='2rem' height='3rem'>
						Nouvelles expériences culinaires ancrées dans la cuisine coréenne
					</StyledTypography>
				</TextContainer>
				<TextContainer sx={{ mt: '3.2rem' }}>
					<Typography variant='body'>91 Rue Amelot, Paris 11e</Typography>
					<Typography variant='body'>Lun-Sam: 12h-14h30 / 19h-22h30</Typography>
				</TextContainer>

				<Grid container spacing={2} mt={{ xs: 3, sm: 7 }} sx={{ px: { xs: 3, sm: 3, md: 0 } }}>
					{cardInfo.map((card, index) => {
						const isLargeCard = index % 2 === 1;
						return (
							<GridItem item xs={6} md={3} key={index} order={{ xs: isLargeCard ? 2 : 1, sm: 0 }} index={index}>
								<Link to='/menu'>
									<StyledCard>
										<StyledCardMedia component='img' image={card.image} alt={card.title} />
										<CardContentStyled>
											<TypographyStyled gutterBottom variant='subTitle' component='div'>
												{card.title}
											</TypographyStyled>
										</CardContentStyled>
									</StyledCard>
								</Link>
							</GridItem>
						);
					})}
				</Grid>
			</Wrapper>
		</Container>
	);
};

export default About;
